import axios from 'axios'
import ReqClient from './interceptor'
import { message } from 'ant-design-vue'
import { RES_CODE } from './config'
import { Spin } from 'iview'

const CancelToken = axios.CancelToken
const source = CancelToken.source()

async function fmtResponse (response) {
  const { status, data } = response
  // HTTP
  if (status === 200) {
    if (data && data.code === RES_CODE) {
      return data.data
    } else if (data && data.msg) {
      message.error(data.msg, 5)
      throw response
    } else {
      message.error('', 5)
      throw response
    }
  } else {
    switch (status) {
      case 403:
        message.error('', 5)
        break
      case 404:
        message.error('', 5)
        break
      case 408:
        message.error('', 5)
        break
      case 500:
        message.error('', 5)
        break
      case 503:
        message.error('-_-sorry', 5)
        break
      case 504:
        message.error('', 5)
        break
      default:
        message.error('! ', 5)
        break
    }
    throw response
  }
}

export async function reqJson (config) {
  try {
    config = Object.assign(config, {
      headers: {
        put: { 'Content-Type': 'application/json' },
        post: { 'Content-Type': 'application/json' },
        patch: { 'Content-Type': 'application/json' }
      },
      cancelToken: source.token
    })
    const response = await ReqClient.request(config)
    return await fmtResponse(response)
  } catch (e) {
    if (axios.isCancel(e)) {
      message.error(e.message)
      Spin.hide()
    }
    return false
  }
}

export async function reqData (config) {
  try {
    config = Object.assign(config, {
      cancelToken: source.token
    })
    const response = await ReqClient.request(config)
    return await fmtResponse(response)
  } catch (e) {
    if (axios.isCancel(e)) {
      message.error(e.message)
      Spin.hide()
    }
    return false
  }
}

export async function reqFormData (config) {
  try {
    config = Object.assign(config, {
      headers: {
        put: { 'Content-Type': 'multipart/form-data' },
        post: { 'Content-Type': 'multipart/form-data' },
        patch: { 'Content-Type': 'multipart/form-data' }
      },
      cancelToken: source.token
    })
    const response = await ReqClient.request(config)
    return await fmtResponse(response)
  } catch (e) {
    if (axios.isCancel(e)) {
      message.error(e.message)
      Spin.hide()
    }
    return false
  }
}

export function cancelRequest () {
  source.cancel('')
}
